<template>
  <div class="m-auto w-full flex flex-wrap justify-center">
    <h1 class="text-center text-3xl font-bold my-4 w-full text-slate-800">
        Chat MGPT4
      </h1>
      <div class="border rounded-lg w-10/12">

      <div id="scroller" class="overflow-auto">
      <div class=" h-96 p-2">
        <div v-for="chat in chats"
             :key="chat.msg"
             class="flex items-end"
             :class="chat.id == 'hum' ? 'justify-end' : ''"
             >
          <div class="flex flex-col space-y-2 text-xs max-w-xs mx-2 order-1 items-end">
             <div>
               <div
                class="w-full font-medium"
                :class="chat.id == 'hum' ? 'px-4 py-2 rounded-lg inline-block rounded-br-none bg-slate-800 text-white' : 'px-4 py-2 rounded-lg inline-block bg-slate-300 text-slate-800'"
                >
                  {{ chat.msg }}
             </div>
              </div>
            </div>
          </div>
      </div>
      </div>
      <div class="h-8 p-2 px-4 pt-4 mb-2 sm:mb-0">
        <div class="relative flex">
          <input
            type="text"
            v-model="newMsg.msg"
            placeholder="Ask . . ."
            class="p-1 border rounded shadow w-full bg-slate-200"
            @keydown.enter="addmsg"
            />
            <button @click="addmsg" type="button" class="inline-flex items-center rounded-full px-1 py-2 transition duration-500 ease-in-out text-white bg-slate-800 hover:bg-slate-500 focus:outline-none">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="h-6 w-6 ml-2 transform rotate-90">
                <path d="M10.894 2.553a1 1 0 00-1.788 0l-7 14a1 1 0 001.169 1.409l5-1.429A1 1 0 009 15.571V11a1 1 0 112 0v4.571a1 1 0 00.725.962l5 1.428a1 1 0 001.17-1.408l-7-14z"></path>
              </svg>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
  data() {
    return {
      counter: 0,
      newMsg: {id: "", msg: ""},
      lastMsg: {id: "", msg: ""},
      chats: [{id: "bot", msg: "Ask me question . . ."}],
    };
  },
  components: {
    
  },
  methods: {
    addmsg() {
      if (this.newMsg.msg !== "") {

        this.puchAns("hum", this.newMsg.msg);

        // setTimeout(() => this.checkAns(this.newMsg.msg.length), 400);
        this.checkAns(this.newMsg.msg.length);
        
        this.newMsg = {id: "", msg: ""};
      }
      this.scrollToEnd();
      // return this.newMsg;
    },
    checkAns(msglnth) {
      if (this.isHellerAsker(this.newMsg.msg)){
        this.hellerAns(Math.floor(Math.random() * 10));
      } else {
        this.lastMsg = this.newMsg;
        if( msglnth % 2 == 0){
            this.puchAns("bot", "Yes");
          } else {
            this.puchAns("bot", "No");
          }
      }
    },
    isHellerAsker(msg){
      if(this.lastMsg.msg == msg) 
      {
        this.counter++;
      } else {
        this.counter = 0;
      }
      if(this.counter >= 3){
        return true;
      } else {
        return false;
      }
    },
    hellerAns(num){
      switch(num) {
        case 0:
         this.puchAns("bot", "Yes, but no");
          break;
        case 1:
          this.puchAns("bot", "I don't know ok!");
          break;
        case 2:
          this.puchAns("bot", "I don't know, how to feel now!");
          break;
        case 3:
          this.puchAns("bot", "do you really want me to answer that?");
          break;
        case 4:
          this.puchAns("bot", "What if I told you I don't know another word?");
          break;
        case 5:
          this.puchAns("bot", "No, but yes");
          break;
        case 6:
          this.puchAns("bot", "Frankly human, I don't know what this means");
          break;
        case 7:
          this.puchAns("bot", "You got me");
          break;
        case 8:
          this.puchAns("bot", "I won't say it");
          break;
        case 9:
          this.puchAns("bot", "Don't like my answer? next time ask someone else first");
          break;
        default:
          this.puchAns("bot", "what do you want me to say?");
      }
    },
    puchAns( chid, chmsg) {
      this.newMsg = {id: chid, msg: chmsg};
      this.chats.push(this.newMsg);
      this.scrollToEnd();
    },
    scrollToEnd() {
      const ele = document.getElementById("scroller");
      ele.scrollTop = ele.scrollHeight;
    },  
  },
}
</script>